@mixin mobilmod () {
  @media (max-width: 420px) {
    @content;
  }
}

@mixin mobile () {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin tablet () {
  @media (min-width: 768px) and (max-width: 1199px) {
    @content;
  }
}

@mixin desktop () {
  @media (min-width: 1200px) {
    @content;
  }
}